@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@400;700&display=swap);
:root {
  --gray-1: #eef4ee;
  --gray-2: #cad1ca;
  --gray-3: #a7afa8;
  --gray-4: #858e87;
  --gray-5: #656f67;
  --gray-6: #46514a;
  --gray-7: #29352e;
  --gray-8: #0e1b14;

  --primary: #297d06;
  --primary-light: #5ca142;
  --primary-dark: #123c02;

  --error: #730b13;
  --error-light: #b4484f;
  --error-dark: #430206;

  font-family: 'Outfit', sans-serif;
  font-size: 16px;
}
.FormItem {
  display: block;
}

.FormItem-label {
  display: block;
  margin-bottom: 0.25em;
  font-size: 0.875em;
}

.Input {
  padding: 0.25em;
  border: 2px solid var(--gray-2);
  background-color: var(--gray-2);
  width: 100%;
  border-radius: 0.25em;
  display: block;
}

.Input_SelectInput option {
  background-color: var(--gray-1);
}

.Input:focus,
.Input__focused {
  border-color: var(--gray-7);
}

.Output-value {
  font-size: 2em;
  display: block;
  line-height: 0.75em;
}

.App {
  background-color: var(--gray-1);
  color: var(--gray-8);
  height: 100vh;
}

.App-content {
  max-width: 100ch;
  padding: 2rem 1rem;
  margin: auto;
}

.App-title {
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.App-inputs {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  flex-wrap: wrap;
  margin: 2rem 0;
}

